import React from "react";
import styled from "styled-components";

import { Introduction } from "./1 introduction/Introduction";
import { Experiences } from "./2 experiences/experiences";
import { UNC } from "./3 education/UNC";

const DesktopPageStyler = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
  transform-origin: top;
  margin: 6%;
`;

export const DesktopPage = () => {
  return (
    <DesktopPageStyler>
      <Introduction />
      <Experiences />
      <UNC />
    </DesktopPageStyler>
  );
};
