import React from "react";
import styled from "styled-components";

import { MobileIntro } from "./mobile/intro/mobileIntro";
import { MobileDinoGlide } from "./mobile/experience/dino glide/mobileDinoGlide";
import { MobileFiruco } from "./mobile/experience/firuco/mobileFiruco";
import { MobileCru } from "./mobile/experience/intern cru/mobileCru";
import { MobileUnc } from "./mobile/education/mobileUnc";

const MobilePageStyler = styled.div`
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transform-origin: top;
  background-color: red;
  width: 100vw;
`;

export const MobilePage = () => {
  return (
    <MobilePageStyler>
      <MobileIntro />
      <MobileDinoGlide />
      <MobileFiruco />
      <MobileCru />
      <MobileUnc />
    </MobilePageStyler>
  );
};
