import React from "react";
import styled from "styled-components";
import { Scrollbars } from "react-custom-scrollbars";
import { Button } from "@mui/material";

const MobileFirucoStyler = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  background-color: rgb(78, 62, 107);
`;

const C1Top = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 1;
  justify-content: center;
  align-items: flex-end;
`;

const FirucoSplash = styled.img`
  display: flex;
  flex: 1;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const C2Bottom = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 20;
  padding-bottom: 3%;
`;

const C2C1Top = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 20;
`;

const C2C1C1Body = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  border: 1px solid rgba(0, 0, 0, 0.5);
  background-color: lightgray;
  font-size: 0.95em;
  overflow: auto;
  border-radius: 20px;
  margin: 6%;
  padding: 4%;
  min-height: 30vh;
  text-align: start;
`;

const CustomScrollbars = styled(Scrollbars)`
  height: 100%;

  /* Customize the scrollbar track */
  .track-vertical {
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
  }

  /* Customize the scrollbar thumb */
  .thumb-vertical {
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 4px;

    &:hover {
      background-color: rgba(0, 0, 0, 0.6);
    }
  }
`;

const C2C2Bottom = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 1;
  background-color: white;
  border-radius: 20px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  margin-left: 1%;
  margin-right: 1%;
`;

const C2C2C1Left = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  flex-grow: 1;
  text-align: center;
`;

const C2C2C1C1Text = styled.h3`
  display: flex;
  flex: 1;
  flex-grow: 1;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding-left: 10%;
`;

const HuntagoSplash = styled.img`
  display: flex;
  flex: 1;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const C2C2C2Right = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0% 10%;
`;

const C2C2C2C1Body = styled.div`
  display: flex;
  flex: 6;
  flex-grow: 1;
  padding: 2%;
  border-radius: 20px;
  margin-bottom: 10%;
  font-size: 0.8em;
  text-align: center;
  padding-top: 10%;
`;

const C2C2C2C2Button = styled.div`
  display: flex;
  flex: 1;
  flex-grow: 1;
  padding: 10%;
  padding-top: 0%;
`;

export const MobileFiruco = () => {
  return (
    <MobileFirucoStyler>
      <C1Top>
        <FirucoSplash
          src="https://i.ibb.co/ssxJ7FW/Firuco-Splash.png"
          alt="placeholder"
        />
      </C1Top>
      <C2Bottom>
        <C2C1Top>
          <C2C1C1Body>
            <CustomScrollbars>
              Firuco is the independent venture I founded in 2022, driven by my
              passion for mobile app development. Originally, I planned to
              release my mobile game, Dino Glide, under my own name. However,
              recognizing the potential for more app ideas, I established Firuco
              as a brand. With Firuco, my focus is on delivering captivating
              digital experiences that transcend boundaries. I am fueled by
              innovation, attention to detail, and a commitment to excellence.
              Beyond Dino Glide, I have conceptualized and developed various
              mobile applications, catering to diverse interests and needs.
            </CustomScrollbars>
          </C2C1C1Body>
        </C2C1Top>
        <C2C2Bottom>
          <C2C2C1Left>
            <C2C2C1C1Text>Soon To Come!</C2C2C1C1Text>
            <HuntagoSplash
              src="https://i.ibb.co/hgQfvvG/huntago.png"
              alt="placeholder"
            />
          </C2C2C1Left>
          <C2C2C2Right>
            <C2C2C2C1Body>
              Project HunTaGo is an innovative social media platform, poised to
              redefine the way college students connect.
            </C2C2C2C1Body>
            <C2C2C2C2Button>
              <Button variant="contained" size="small">
                CLICK TO CHECK BLOG FOR UPDATES
              </Button>
            </C2C2C2C2Button>
          </C2C2C2Right>
        </C2C2Bottom>
      </C2Bottom>
    </MobileFirucoStyler>
  );
};
